///<reference path="../../../../node_modules/@angular/core/src/metadata/directives.d.ts"/>
import {ChangeDetectorRef, Component, EventEmitter, Output, ViewChild} from '@angular/core';
import {CamComponent} from '../cam.component';

declare var MediaRecorder: any;
declare var GIF: any;

@Component({
  selector: 'app-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.scss']

})

export class VideoComponent extends CamComponent {
  @ViewChild('videoPreviewElement') videoPreviewElement;
  @ViewChild('canvas') canvas;
  @ViewChild('gifPreview') gifPreview;

  /* EventEmitter that sends the video */
  @Output() video_done = new EventEmitter<Blob>();
  @Output() previewAvailable = new EventEmitter<Blob>();

  constructor(ref: ChangeDetectorRef) {
    super(ref);
  }

  /*
      Adjustable settings
   */
  video_length = 5; // length of chunks in ms
  chunk_length = 1000;
  /*
     END of Adjustable settings
  */
  stream: MediaStream;
  video: Blob = null;
  gifVideo = null;
  timer = this.video_length;

  recording = false;
  preview_hidden = true;
  gif_preview_hidden = true;

  mediaRecorder: any;
  chunks = [];
  width = 0;
  height = 0;
  currFrame = 0;
  encoder = null;
  context = null;
  fps = 18;
  maxW = 600;
  maxH = 480;

  startCamera() {
    this.preview_hidden = true;
    this.gif_preview_hidden = true;
    this.timer = this.video_length;
    super.startCamera();
  }

  startTakingVideo() {
    if (this.recording) {
      return;
    }
    this.chunks = [];
    // Timer Init
    this.timer = this.video_length;
    this.recording = true;
    this.video = null;

    this.mediaRecorder = new MediaRecorder(this.stream);

    this.mediaRecorder.ondataavailable = (e) => this.pushChunk(e);
    this.mediaRecorder.onstop = () => this.videoFinished();

    this.mediaRecorder.start(this.chunk_length);
    // The MediaRecorder state must be 'recording'
  }

  startTakingGif() {
    this.currFrame = 0;
    this.width = this.streamView.nativeElement.videoWidth;
    this.height = this.streamView.nativeElement.videoHeight;


    const r = this.width / this.height;

    if (this.width > this.maxW) {
      this.width = this.maxW;
      this.height = this.width / r;
    }

    if (this.height > this.maxH) {
      this.height = this.maxH;
      this.width = this.height * r;
    }
    this.canvas.nativeElement.width = this.width;
    this.canvas.nativeElement.height = this.height;

    this.context = this.canvas.nativeElement.getContext('2d');
    this.changeDetectorRef.detectChanges();
    this.encoder = new GIF({
      workers: 18,
      quality: 20,
      workerScript: 'assets/gif.worker.js'
    });
    this.encoder.on('finished', blob => this.showGifPreview(blob));
    this.takeSnapshot();
  }

  takeSnapshot() {
    if (this.currFrame < this.video_length * this.fps) {
      setTimeout(() => this.takeSnapshot(), 1000 / this.fps);
    } else {
      this.stopStreams();
      this.encoder.render();

      return;
    }
    this.currFrame++;
    this.timer = Math.floor(this.video_length - (this.currFrame * (1 / this.fps)));
    // this.takeImage();
    this.context.drawImage(this.streamView.nativeElement, 0, 0, this.width, this.height);
    this.encoder.addFrame(this.canvas.nativeElement, {copy: true, delay: 1000 / this.fps, dither: 'Atkinson'});
  }

  pushChunk(chunk) {
    if (this.timer === 0) {
      return;
    }
    /* Update Timer */
    this.timer--;
    this.changeDetectorRef.detectChanges();
    /* End Update */
    this.chunks.push(chunk.data);
    /* Check if time is over */
    if (this.timer === 0) {
      this.stopTakingVideo();
    }
    /* End check */
  }

  videoFinished() {
    this.video = new Blob(this.chunks, {'type': this.mediaRecorder.mimeType});
    this.previewAvailable.emit(this.video);
    this.showVideoPreview(this.video);
  }

  showVideoPreview(blob) {
    this.preview_hidden = false;
    this.changeDetectorRef.detectChanges();

    this.stopStreams();
    this.videoPreviewElement.nativeElement.src = URL.createObjectURL(blob);
    this.videoPreviewElement.nativeElement.load();
    /* Show Preview */
    this.videoPreviewElement.nativeElement.onloadeddata = () => this.videoPreviewElement.nativeElement.play();
  }

  showGifPreview(blob) {
    this.preview_hidden = false;
    this.gif_preview_hidden = false;
    this.stopStreams();

    const reader = new FileReader();
    reader.onloadend = () => {
      this.gifVideo = reader.result;
      this.changeDetectorRef.detectChanges();
    };
    reader.readAsDataURL(blob);

  }

  getBackground() {
    if (!this.gifVideo) {
      return '';
    }
    return 'url(' + this.gifVideo + ')';
  }

  acceptPreview() {
    this.stopCamera();
    this.video_done.emit(this.video);
  }

  rejectPreview() {
    this.preview_hidden = true;
    this.gif_preview_hidden = true;
    this.startCamera();
  }

  replayPreview() {
    this.videoPreviewElement.nativeElement.play();
  }

  numberPadding(num) {
    return num >= 10 ? num : '0' + num;
  }

  stopTakingVideo() {
    this.mediaRecorder.stop();
    this.recording = false;
  }

}




