import {Component, OnInit, ViewChild} from '@angular/core';
import {DataManagerService} from '../../services/data-manager.service';

@Component({
  selector: 'app-step-danno-fabbricato',
  templateUrl: './step-danno-fabbricato.component.html',
  styleUrls: ['./step-danno-fabbricato.component.scss']
})
export class StepDannoFabbricatoComponent implements OnInit {

  locali = [];
  selectedID = -1;
  selectedLocale = {};

  constructor(private dataManager: DataManagerService) {
  }
  setLocale(locale) {
    this.selectedLocale = locale;
    this.selectedID = locale.ID;
  }
  confirmChanges() {

  }

  ngOnInit() {
    this.dataManager.getLocaliFabbricato(success => {
      this.locali = success;
    });
  }


}



