import {ChangeDetectorRef, Component, EventEmitter, Output, ViewChild} from '@angular/core';
import {CamComponent} from '../cam.component';

@Component({
  selector: 'app-photo',
  templateUrl: './photo.component.html',
  styleUrls: ['./photo.component.scss']
})

export class PhotoComponent extends CamComponent {
  @Output() photo_done = new EventEmitter<Blob>();
  @ViewChild('streamView') streamView;

  constructor(ref: ChangeDetectorRef) {
    super(ref);
  }

  /*
    Adjustable settings
 */
  /*
     END of Adjustable settings
  */
  photo: Blob = null;
  image_data = null;
  preview_hidden = true;

  takePhoto() {
    // Choose picture size (can be video width/height
    const width = this.streamView.nativeElement.videoWidth,
      height = this.streamView.nativeElement.videoHeight;

    const canvas = document.createElement('canvas');
    const context = canvas.getContext('2d');

    canvas.width = width;
    canvas.height = height;
    context.drawImage(this.streamView.nativeElement, 0, 0, width, height);
    const image_url = canvas.toDataURL('image/png');
    this.image_data = image_url;
    this.photo = this.dataURLToBlob(image_url);
    this.changeDetectorRef.detectChanges();

    this.preview_hidden = false;
    this.stopStreams();
  }

  startCamera() {
    this.preview_hidden = true;
    super.startCamera();
  }

  acceptPhoto() {
    this.photo_done.emit(this.photo);
    this.stopCamera();
  }

  rejectPhoto() {
    this.startCamera();
  }

  getBackground() {
    return 'url(' + this.image_data + ')';
  }

  dataURLToBlob(dataURL) {
    const BASE64_MARKER = ';base64,';
    if (dataURL.indexOf(BASE64_MARKER) === -1) {
      const parts_aux = dataURL.split(',');
      const contentType_aux = parts_aux[0].split(':')[1];
      const raw_aux = parts_aux[1];

      return new Blob([raw_aux], {type: contentType_aux});
    }

    const parts = dataURL.split(BASE64_MARKER);
    const contentType = parts[0].split(':')[1];
    const raw = window.atob(parts[1]);
    const rawLength = raw.length;

    const uInt8Array = new Uint8Array(rawLength);
    let i;
    for (i = 0; i < rawLength; ++i) {
      uInt8Array[i] = raw.charCodeAt(i);
    }

    return new Blob([uInt8Array], {type: contentType});
  }

}




