import {Component, Input, OnInit} from '@angular/core';
import {DomSanitizer} from '@angular/platform-browser';
import {Overlay, OverlayModule} from '@angular/cdk/overlay';
import {ComponentPortal} from '@angular/cdk/portal';

@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.scss']
})
export class GalleryComponent implements OnInit {

  images = [];
  video = null;
  @Input() size: number;
  photoHidden = true;
  photoPreviewHidden = true;
  selectedImage = '';

  constructor(private sanitizer: DomSanitizer, private overlay: Overlay) {
  }

  ngOnInit() {
  }

  addImage(image: Blob) {
    const win: any = window;
    this.images.push((win.URL || win.webkitURL).createObjectURL(image));
    this.photoHidden = true;
  }

  sanitize(url) {
    if (!url) {
      return;
    }
    return this.sanitizer.bypassSecurityTrustUrl(url);
  }

  sanitizeCSS(url) {
    if (!url) {
      return;
    }
    return this.sanitizer.bypassSecurityTrustStyle('url("' + url + '")');
  }

  openImage(image) {
    this.photoPreviewHidden = false;
    this.selectedImage = image;
  }

  removePhoto() {
    const idx = this.images.indexOf(this.selectedImage);
    if (idx > -1) {
      this.images.splice(idx, 1);
    }

    this.photoPreviewHidden = true;
    this.selectedImage = '';
  }
}



