import {Component, OnInit, ViewChild} from '@angular/core';
import {CookieService} from 'angular2-cookie/core';
import {DataService} from '../../services/dataService/data.service';
import {DataManagerService} from '../../services/data-manager.service';

@Component({
  selector: 'app-step-data-sinistro',
  templateUrl: './step-data-sinistro.component.html',
  styleUrls: ['./step-data-sinistro.component.scss']
})
export class StepDataSinistroComponent implements OnInit {
  @ViewChild('calendarModal') calendarModal;

  constructor(private dataManager: DataManagerService) {

  }

  userAccuracy = '2';
  dataSinistro = 'Nessuna data selezionata';
  dataSelected = false;
  hideData = true;
  hideCalendar = true;

  ngOnInit() {
    this.dataSelected = false;
  }

  changeAccuracy(newAcc) {
    this.userAccuracy = newAcc.value;

    if (this.userAccuracy !== '2' && !this.dataSelected) {
      this.calendarModal.show();
    }
    this.hideData = this.userAccuracy === '2';

  }

  dateSelected(date) {
    this.hideCalendar = true;
    this.hideData = false;
    this.dataSelected = true;
    this.dataSinistro = date._value.toLocaleDateString();
    this.calendarModal.hide();
  }

  editDate() {
    this.calendarModal.show();
  }

  confirmChanges() {
    this.dataManager.getCurrentUserDossierNominativo(doss => {
      console.log(doss);
    }, null);
  }
}



