import { Component, ElementRef, AfterViewInit, ViewChild, Input } from '@angular/core';
import * as OT from '@opentok/client';
import {PhotoComponent} from '../../cam/photo/photo.component';
import {VideoComponent} from '../../cam/video/video.component';
import {DataService} from '../../services/dataService/data.service';
import {NgForage, NgForageCache} from 'ngforage';
import {Router} from '@angular/router';
import {CookieService} from 'angular2-cookie/core';
import {Entity, EntityManager} from 'breeze-client';
import config from '../../../config';
import {NotificationComponent} from '../../notification/notification.component';
import {NotificationType} from '../../notification/notification-type.enum';
import {BarcodeReaderComponent} from '../../barcodeReader/barcode-reader.component';

@Component({
  selector: 'app-subscriber',
  templateUrl: './subscriber.component.html',
  styleUrls: ['./subscriber.component.css']
})

export class SubscriberComponent implements AfterViewInit {
  @ViewChild('subscriberDiv') subscriberDiv: ElementRef;
  @ViewChild('buttonsDiv') buttonsDiv: ElementRef;

  @Input() session: OT.Session;
  @Input() stream: OT.Stream;
  @Input() appPhoto: PhotoComponent;
  @Input() appVideo: VideoComponent;
  @Input() barcodeReader: BarcodeReaderComponent;
  @Input() notification: NotificationComponent;

  constructor(private dataService: DataService,
              private readonly ngf: NgForage,
              private readonly cache: NgForageCache,
              private router: Router,
              private cookieService: CookieService) { }

  ngAfterViewInit() {
    this.buttonsDiv.nativeElement.style.width = window.screen.width * 0.9 + 'px';
    const subscriber = this.session.subscribe(this.stream, this.subscriberDiv.nativeElement, {
      width: (0.9 * window.screen.width),
      height: (0.9 * window.screen.width * 9 / 16)
    }, (err) => {
      if (err) {
        alert(err.message);
      }
    });
  }

  uploadBlob(self: any, blob: Blob, code?: string) {
    self.ngf.getItem('videoContext').then(serializedContext => {
      if (serializedContext === undefined) {
        console.log('videoContext not found');
        self.cookieService.removeAll();
        self.router.navigateByUrl('/login');
      } else {
        const context: EntityManager = new EntityManager(config.SERVER_SVILUPPO);
        context.importEntities(serializedContext);
        console.log('videoContext imported');
        console.log(context);
        const dossierNominativo: Entity = context.getEntities('DossierNominativoView')[0];
        console.log(blob);
        console.log(code);
        self.dataService.createAndUploadAttachment(context,  dossierNominativo, blob, code).then(s => {
          console.log('blob uploaded');
          self.notification.show('Upload completato', 'L\'allegato  stato caricato correttamente!', NotificationType.Success);
          setTimeout(() => {
            self.notification.hide();
          }, 4000);
        }).catch(e => {
          console.log(e);
          // alert('impossible to notification the photo')
          self.notification.show('Upload non riuscito', 'Impossibile caricare l\'allegato', NotificationType.Error);
          setTimeout(() => {
            self.notification.hide();
          }, 4000);
        });
      }
    }).catch(e => {
      console.log(e);
      alert('Impossible to use localforage indexedDB');
    });
  }

  startPhotoCamera() {
    this.appPhoto.startCamera();
    this.appPhoto.photo_done.subscribe(blob => this.uploadBlob(this, blob));
  }

  startVideoCamera() {
    this.appVideo.startCamera();
    this.appVideo.video_done.subscribe(blob => this.uploadBlob(this, blob));
  }

  startBarcodeReader(){
    this.barcodeReader.startCamera();
    this.barcodeReader.code_found.subscribe(({'photo': blob, 'code': code}) => this.uploadBlob(this, blob, code));
  }

  startDocumentReader(){
    this.appPhoto.startCamera();
    this.appPhoto.photo_done.subscribe(blob => this.uploadBlob(this, blob)); // probably we have to add something to say that is a document
  }
}



