import {Component, OnInit, ViewChild} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {DlDateTimePickerDateModule} from 'angular-bootstrap-datetimepicker';
import {MatRadioModule} from '@angular/material/radio';

@Component({
  selector: 'app-autoperizia',
  templateUrl: './autoperizia.component.html',
  styleUrls: ['./autoperizia.component.scss']
})
export class AutoperiziaComponent implements OnInit {

  @ViewChild('photo_cam') photoCam;
  @ViewChild('video_cam') videoCam;
  @ViewChild('barcode_cam') barcodeCam;

  isLinear = false;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;

  constructor(private _formBuilder: FormBuilder) {
  }

  ngOnInit() {
    this.firstFormGroup = this._formBuilder.group({
      firstCtrl: ['', Validators.required]
    });
    this.secondFormGroup = this._formBuilder.group({
      secondCtrl: ['', Validators.required]
    });
  }

  requirePicture() {
    this.photoCam.startCamera();
  }

  requireVideo() {
    this.videoCam.startCamera();
  }

  requireBarcode() {
    this.barcodeCam.startCamera();
  }


  myfun(video) {
    console.log(video);
  }
}



