import {Component, OnInit, ViewChild} from '@angular/core';

@Component({
  selector: 'app-step-particolare-danno-fabbricato',
  templateUrl: './step-particolare-danno-fabbricato.component.html',
  styleUrls: ['./step-particolare-danno-fabbricato.component.scss']
})
export class StepParticolareDannoFabbricatoComponent implements OnInit {

  @ViewChild('video_cam') videoCam;
  accuracy = '';
  video = {};


  constructor() { }

  ngOnInit() {
  }

  take_video_danno(accuracy) {
    this.accuracy = accuracy;
    this.videoCam.startCamera();
  }

  set_video(video) {
    this.video = video;
    document.getElementById('particolare-danno-ok').hidden = false;
  }
  confirm() {

  }

}



