import {ChangeDetectorRef, Component, OnInit, ViewChild} from '@angular/core';
import {DataManagerService} from '../../services/data-manager.service';

@Component({
  selector: 'app-step-causa-sinistro',
  templateUrl: './step-causa-sinistro.component.html',
  styleUrls: ['./step-causa-sinistro.component.scss']
})
export class StepCausaSinistroComponent implements OnInit {
  @ViewChild('causeList') causeList;

  userAccuracy = '0';
  hideButtons = true;
  hideGallery = false;
  cause = [];
  causeHidden = [];
  selectedID = null;

  slickConfig = {
    'slidesToShow': 1, 'slidesToScroll': 1, 'dots': true
  };

  constructor(private dataManager: DataManagerService, private ref: ChangeDetectorRef) {
  }

  changeAccuracy(newAcc) {
    this.userAccuracy = newAcc.value;
    this.hideButtons = this.userAccuracy === '2';
    this.ref.detectChanges();
    this.cause = [[]];
    this.ref.detectChanges();
    this.cause = [];
    this.ref.detectChanges();
    this.cause = this.hideButtons ? [] : this.causeHidden;
  }

  setIDCausa(id) {
    this.selectedID = id;
  }

  fotoImpossibile() {
    this.hideGallery = true;
  }

  confirmChanges() {

  }

  chunkify(a, n, balanced) {

    if (n < 2) {
      return [a];
    }

    const len = a.length,
      out = [];
    let size, i = 0;

    if (len % n === 0) {
      size = Math.floor(len / n);
      while (i < len) {
        out.push(a.slice(i, i += size));
      }
    } else if (balanced) {
      while (i < len) {
        size = Math.ceil((len - i) / n--);
        out.push(a.slice(i, i += size));
      }
    } else {
      n--;
      size = Math.floor(len / n);
      if (len % size === 0) {
        size--;
      }
      while (i < size * n) {
        out.push(a.slice(i, i += size));
      }
      out.push(a.slice(size * n));
    }

    return out;
  }


  ngOnInit() {

    this.dataManager.getCauseSinistro(success => {
      this.causeHidden = this.chunkify(success, 3, true);
    });

    this.cause = [];
  }

}



